
body {
  text-align: center; }

.red-background {
    width: 100%;
    height: 100vw;
    background: red;
    position: absolute;
    z-index: -40;
    display: none; }

#ash {
    width: 95vw;
    top: 193vw;
    left: -1vw;
    position: absolute; }

#ash2 {
  display: none; }

#speech-bubble {
  display: none;
  position: absolute;
  right: 6vw;
  top: 64vw; }

#speech-bubble2 {
  visibility: hidden; }


.pallet-button {
  width: 30vw;
  font-size: 25vw;
  text-align: center;
  position: relative;
  left: 33vw;
  display: none;
  color: #3531db; }

.pallet-button:hover {
  color: #04c629; }

.background-div {

  position: absolute;
  background-image: url('http://satoshipedia.altervista.org/wp-content/uploads/2016/04/Masara-Town.png');
  background-size: cover;
  min-width: 100%;
  height: 150vh; }


.background-div2 {
  position: absolute;
  background-image: url('https://i.ytimg.com/vi/LI1fkSSbRMk/maxresdefault.jpg');
  display: none;
  width: 100vw;
  background-position-y: 0vw;
  background-position-x: 0vw;
  background-repeat: no-repeat;
  height: 180vh; }

.barrydos {
  display: none;
  position: absolute;
  z-index: 1;
  left: 7vw;
  top: 12vw; }

.barrydos-shadow {
  display: none;
  position: absolute;
  z-index: 2;
  left: 7vw;
  top: 12vw; }


@media (min-width: 925px) {
  .pallet-button {
    font-size: 10vw; }

  #speech-bubble {
    visibility: hidden; }

  #speech-bubble2 {
    visibility: visible;
    display: none;
    position: absolute;
    right: 16vw;
    top: 11vw;
    width: 23vw; }


  #ash {
    display: none; }

  #ash2 {
    width: 50vw;
    top: 193vw;
    left: 19vw;
    position: absolute;
    display: unset; } }

.ash-pedestal {
  width: 100%;
  height: 100vh;
  position: relative;
  background: linear-gradient(90deg, #664504, #b99f7c);
  border-radius: 30px;
  margin: 0px 85px 0px 121px;
  top: 56vw;
  right: 120px; }
