body {
  text-align: center;
  background-image: url('http://i.imgur.com/JAxMcBl.jpg');
  background-attachment: fixed; }


img {
  position: relative; }


.party-time {
  animation: colors 3s linear alternate infinite; }


@keyframes colors {
  14.28% {
    background: #fe0f0f; }
  28.57% {
    background: #fe890f; }
  42.85% {
    background: #e6ff1c; }
  57.13% {
    background: #00d509; }
  71.41% {
    background: #0012ff; }
  85.69% {
    background: #7800ff; }
  100% {
    background: #9e00ff; } }




.weather-div {
  position: relative;
  // right: 100vw
  width: 25vw;
  height: 42vw;
  background: rgba(97, 136, 198,.5);
  margin: 30px 5px 11px -3px;
  font-size: 4vw;
  border: 5px solid black; }

.weather-div img {
 }  // display: none


.weather-div-container {
  font-size: 3vw; }

.weekDayContainer {
  position: relative;
  top: 3vw; }

.mother-div {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap; }


#poke-party {
  width: 90vw;
  margin: 20px 0px 0px 0px;
  left: 0vw; }

.daysOfTheWeekContainer {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  font-size: 3vw;
  position: relative;
  top: 3vw; }




.weather-div img {
  right: 1vw; }

.day4 {
  display: none; }
.day5 {
  display: none; }



@media (min-width: 500px) {
  .weather-div {
    height: 35vw; }
  .weather-div img {
    right: 1vw; } }

@media (min-width: 575px) {
  .weather-div {
    height: 30vw;
    width: 23vw; }
  .weather-div img {
    right: 3vw; } }

@media (min-width: 670px) {
  .daysOfTheWeekContainer {
    display: flex;
    justify-content: space-around;
    font-size: 3vw;
    position: relative;
    top: 3vw; }

  .weather-div {
    height: 20vw;
    width: 15vw; }
  .weather-div img {
    right: 2vw;
    width: 19vw;
    bottom: 3vw; }
  .mother-div {
    display: flex;
    flex-wrap: nowrap; }

  .day4 {
    display: unset; }
  .day5 {
    display: unset; } }

@media (min-width: 1000px) {
  #poke-party {
    width: 40vw;
    bottom: -3vw; }
  .weather-div img {
    bottom: 3vw; }

  .weather-div {
    margin: 30px 36px 11px 24px; }

  .poke-title {
    top: 10vw; } }
