img {
  position: relative;
  width: 30vw; }

p {
  font-size: 7vw; }

.poke-title {
  width: 90vw;
  margin: 3vw 0vw 0vw 0vw;
  animation: flip 1.5s forwards linear;
  position: relative;
  right: 50vw; }

.poke-pic {}


.home-weather-div {
    background: rgba(65, 70, 200, 0.5);
    margin: 0vw 30vw 0vw 30vw;
    padding: 0vw 0vw 4vw 0vw;
    border: 5px solid black; }

@keyframes flip {
  0% {
    transform: rotate3d(0,0,0,0deg);
    left: -50vw; }
  100% {
     transform: rotate3d(1,1,1,360deg);
     left: 0vw; } }



.taskbar:hover {
  color: #7019d1;
  text-decoration: none; }

@media (min-width: 575px) {
  .poke-title {
    width: 55vw; }
  .current-weather-description {
    font-size: 5vw; }
  .temp {
    font-size: 2vw; } }




@media (min-width: 575px) {
  .home-weather-div {
    height: 31vw;
    margin: 0vw 40vw 0vw 40vw; }
  .poke-pic {
    width: 19vw;
    bottom: 4vw; }
  .two-stats {
    font-size: 3vw; } }



@media (min-width: 850px) {
  .poke-pic {
    width: 14vw; }
  .home-weather-div {
    height: 22vw; } }


@media (min-width: 1000px) {
  .poke-title {
    top: 0vw; } }





@media (min-width: 1250px) {
  .current-weather-description {
    font-size: 3vw;
    top: 1vw;
    position: relative; }

  .temp {
    font-size: 2vw; }



  .poke-title {
    top: -2vw; }
  .home-weather-div {
    height: 20vw;
    position: relative;
    bottom: 2vw; }
  .current-weather {
    position: relative;
    bottom: 3vw; }
  .poke-pic {
    width: 13vw;
    bottom: 2vw; } }
